.improve-skills{
    .col.img{
        img{
            width: 100%;
            object-fit: cover;
            border-radius: 5px;
        }

    }
    .col.typography{
        padding-left: 2em;
        .skill-item{
            border-left: .2em solid var(--primary-color);
            margin-bottom: .5em;
            padding: .1em 0 .1em .5em;
            color: var(--text-light);
            font-weight: bold;
        
        }
        .btn{
            margin-top: .75em;
        }
    }
    
}