.funny-heading {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Playfair Display', serif;
    font-size: 36px;
    color: var(--text-color); /* Vibrant color */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2); /* Subtle shadow effect */
    padding-bottom: 15px;
    .highlighted-text {
        color: var(--primary-color);
       

      }
  }
  .recipe-container{
    width: 80%;
    margin: 25px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
    grid-gap: 2rem;
    

  }
  .para{
        font-size: 1.2em;
        line-height: 1.3;
        color: var(--text-color);
        svg{
            color: var(--primary-color);
            font-size: 1.5em;
            margin-right: .5em;
        }
    
  }
  .recipe-card{
    width: 180px;
    color: var(--text-color);
    overflow: hidden;
    border-radius: 1rem ;
    background-color: var(--background-color);
    padding: 5px;
    box-shadow: 0 2px 6px var(--shadow-color);
    &:hover{
        transform: scale(1.1); /* Scale the card by 5% on hover (adjust as needed) */
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); 
    }
    img{
        width: 105%;
        border-radius: 5px;
        overflow: hidden;
        margin-left : -3.5px;
        margin-top: -4.5px;
    }
    h3{
        font-size: 18px;
    }
  }
  .mani{
    width: 80%;
    margin: 0 auto;
    text-align: center;
  }
  .indexContainer{
    display: flex;
    margin: 2.5rem 0;
    margin-left: -50px;
}
.numBox {
    cursor: pointer;
    width: 50px;
    height: 40px;
    
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--text-color);
    margin: 0 1px 0 10px;
    &:hover{
        color: var(--primary-color);
       
    }
}
  
.search-box{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    font-style: to-upper-case($string: "");
    
    input, .btn {
        height: 35px;
    }
    input{
        min-width: 250px;
        padding: 0 , .5em;
        border-radius: 5px 0 0 5px;
        outline: none;
        
        
        border: 1px solid #4d4d4d;
    }
    .btn{
        padding-block: 0;
        padding-inline: .75em;
        border-radius: 0 5px 5px 0;
    }
}
h1{
    color: var(--primary-color);
    padding-left: 2px;
   

}
.rescontent{
    .infing {
        font-size: var(--fontsize);
        font-family: 'Roboto', sans-serif;
        display: flex; /* Use flexbox */
        justify-content: space-between;
        border-radius: 5px;
        color: var(--text-color);
        .rec-info{
            justify-content: center;
            align-items: center;
            .inner-content{
                h2,h3{
                    padding-left: 10px;
                }
                span{
                    color: var(--primary-color);
                }
            }

        }
        .ingredients{
            h2{
                color: var(--primary-color);
            }
        }
        
        /* Space items evenly along the main axis */
      }
      
      .rec-info,
      .ingredients {
        width: 48%; /* Adjust the width as needed */
        box-sizing: border-box; 
        
        
        
        /* Include padding and border in the width calculation */
      }
      img{
        justify-content: center;
        padding-left: 2px;
        padding-bottom: 10px;
        width: 100%; 
        max-width: 300px; 
        border-radius: 10px 10px 10px 10px;
        
        height: auto;
        overflow: hidden;
        
      }
    .instructions{
        
        border-radius: 5px;
        margin-top: 50px;
        padding-left: 2px;
        padding-right: 2px;
        padding-bottom: 5px;
        margin-bottom: 50px;
        color: var(--text-color);
        h2{
            color: var(--primary-color);
        }

    }
    iframe {
        width: 100%;
        height: 400px;
        border: none;
        box-shadow: 0 5px 10px var(--shadow-color);
        border-radius: 5px;
      }
    
}
