:root{
    --background-color : #fff;
    --background-light : #fff;
    --shadow-color : rgba(0,0,0,0.2);
    --primary-color : rgb(255,0,86);
    --text-color : #0A0A0A;
    --text-light : #575757;
    --fontsize: 16px;
    --animation-speed : 1;
}
@function getAnimationDuration($duration : number){
    @return calc($duration * var(--animation-speed));
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box; 
}
body{
    font-size: var(--fontsize);
    font-family: 'Roboto', sans-serif;
    padding-top: 55px;
    background-color: var(--background-light);
}
a{
    text-decoration: none;
}
img{
    object-fit: cover;
}
.btn{
    cursor: pointer;
    border: none;
    outline: none;
    background-color: var(--primary-color);
    color: #fff;
    padding: .75em 1em;
    text-transform: uppercase;
    font-size: 1em;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 3px;
    box-shadow: 0 2px 6px var(--shadow-color);
    &:hover{
        box-shadow: 0 1px 3px var(--shadow-color);
        background-color: #0A0A0A;
    }


}

@import  "partials/recipestyle";
@import "partials/navbar";
.section{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3vw;
    &.d-block{
        display: block;
    }
        .title{
        color: var(--text-color);
        font-size: 2.5em;
        margin-bottom: .25em;
        font-weight: 900;
    }
    .info{
        color: var(--text-light);
        font-size: 1em;
        letter-spacing: 1px;
        line-height: 1.3;
        margin-bottom: 1.25em;
    }
    .col{
        width: 50%;
    }
}
.footer{
    background-color: #0f0f0f;
    color: #fff;
    padding-block: 3em;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-gap: 1em;
    .footer-section{
        p{
            opacity: .9;
            margin-bottom: .5em;
        }
        p.title{
            opacity: 1;
            font-size: 1.1em;
            font-weight: bold;
            margin-bottom: 1em;
        }
    }
}

.main{
    padding-block: 3vw;
}
.container{
    padding-inline: 25vw;
}
@import "partials/chefsection";
@import "partials/quotes";
@import "partials/improveskills";
@import "partials/heroes";
@import "partials/animations";
.options-container{
    box-shadow: 0 5px 10px var(--shadow-color);
    padding: 1em;
    border-radius: 5px;
    border-left: .5em solid var(--primary-color);
    background-color: var(--background-light);
    margin-top: 1em;
    display: flex;
    gap: 1em;
    .option{
        height: 3.5em;
        width: 3.5em;
        box-shadow: 0 3px 6px var(--shadow-color);
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover{
            transform: scale(1.1); /* Scale the card by 5% on hover (adjust as needed) */
            box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); 
        }
        &.light{
            background-color: #fff;
        }
        &.dark{
            background-color: rgb(29, 29, 29);
        }
        .check{
            height: 2.5em;
            width: 2.5em;
            background-color: var(--primary-color);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 0 2px 5px var(--shadow-color);

        }
        svg{
            height: 1.5em;
            width: 1.5em;
        }
    
    }
    
}
.span{
   
        display: inline-block;
        margin-left: .5em;
}

.d-block h2{
    font-size: 1.75em;
    color: var(--text-color);
}
@media screen and (max-width: 1280px){
    .container{
        padding-inline: 18vw;
    }
    .ingridients{
        margin-left: -100px;
    }
    
    
}
@media screen and (max-width: 1050px){
    .container{
        padding-inline: 12vw;
    }
}
@media screen and (max-width: 768px){
    .navbar{
        .navlinks{
            display: none;
        }
        .sidebar-btn{
            display: block;
        }
    }
    .section{
        .col{
            width: 100%;
            padding: 0 !important;
            text-align: center;
            margin-bottom: 2em;

        }

    }
    .indexContainer{
        margin-right : -15px;

    }
    .numBox{
        margin: 1.5px;
    }
    .skill-item{
        display: inline-block;
        margin: .75em;
    }
    .section.improve-skills .btn{
        display: block;
        margin-left: 50%;
        transform: translateX(-50%);
    }
    .chef-card{
        box-shadow: 0 5px 10px var(--shadow-color);
    }
    .container{
        padding-inline: 6vw;
    }
    .footer{
        grid-template-columns: repeat(4, 1fr);
        .footer-section:nth-child(1){
            grid-column: span 4;
        }
        .footer-section:nth-child(2),.footer-section:nth-child(3){
            grid-column: span 2;
        }
         
    }

    
}
@media screen and (max-width: 480px){
    .container{
        padding-inline: 3vw;
    }
}